










import { Component, Vue, Watch } from 'vue-property-decorator';
import AboutUs from '@/application/Aboutus/View/AboutUs.vue';
import CustomerService from '@/application/Aboutus/View/CustomerService.vue';
import BusinessCoop from '@/application/Aboutus/View/BusinessCoop.vue';
import Disclaimer from '@/application/Aboutus/View/Disclaimer.vue';

@Component({
  components: {
    AboutUs,
    CustomerService,
    BusinessCoop,
    Disclaimer,
  },
})
export default class About extends Vue {
  private title: any = '';
  private mounted() {
    this.title = this.$route.params.title;
  }
  @Watch('$route')
  private routeChange(url: any) {
    this.title = url.params.title;
  }
}
